import { Vector3 } from "./Vector3";

/*
    
     ax + by + cz + d = 0   

    1. 
    -  P(x, y, z)
    -  n = (a, b, c)
    -  P(x, y, z)

    2. 
     PP = (x - x, y - y, z - z)  n  0n  PP = 0

    3. 
    a(x - x) + b(y - y) + c(z - z) = 0

    ax + by + cz - (ax + by + cz) = 0

    4.  d
     d = - (ax + by + cz) P  d ax + by + cz + d = 0


    
     d = -(ax + by + cz) 

    1. 
    ax + by + cz + d = 0
     P(x, y, z) ax + by + cz + d = 0 d = -(ax + by + cz)
     O(0,0,0)  OP P  n = (a,b,c) 

    2. 
     OP = (x, y, z) n 
     = |OP  n| / |n|
     |OP  n| = |ax + by + cz|
     |n| = (a + b + c)

    3.  d 
     d = -(ax + by + cz) |ax + by + cz| = |-d| = |d|
     = |d| / (a + b + c)
 */
export class Plane {
    /**
     * ax + by + cz + d = 0
     * (x, y, z)
     * (a, b, c)
     *  = |d|  (a + b + c)
     * @param a X
     * @param b Y
     * @param c Z
     * @param d  d     
     */
    constructor(
        public a: number,
        public b: number,
        public c: number,
        public d: number
    ) { }

    /**
     * 1
     */
    public normalize(): void {
        const normalLength = Math.sqrt(this.a ** 2 + this.b ** 2 + this.c ** 2);
        if (normalLength < Number.EPSILON) return; // 0

        this.a /= normalLength;
        this.b /= normalLength;
        this.c /= normalLength;
        this.d /= normalLength;
    }

    /**
     * 
     * @param point 
     * @returns 0
     */
    public distanceToPoint(point: Vector3): number {
        /*
              

            
            ax + by + cz + d = 0 n = (a, b, c)
            P(x, y, z)ax + by + cz + d = 0
            P(x, y, z)

             = 
             P  PP  n 

             PP 
            PP = (x - x, y - y, z - z)

            
             = |PP  n| / |n|
            

            
            
            PP  n = a(x - x) + b(y - y) + c(z - z)
            
            = ax + by + cz - (ax + by + cz)
             ax + by + cz = -d
            PP  n = ax + by + cz + d
            |PP  n| = |ax + by + cz + d|

            
            |n| = (a + b + c)

            
             = |ax + by + cz + d| / (a + b + c)
         */
        return this.a * point.x + this.b * point.y + this.c * point.z + this.d;
    }
}